@-webkit-keyframes fadeInUp {
    from {
      opacity: 0;
      -webkit-transform: translate3d(0, 100%, 0);
      transform: translate3d(0, 100%, 0);
    }
  
    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
  }
  
  @keyframes fadeInUp {
    from {
      opacity: 0;
      -webkit-transform: translate3d(0, 100%, 0);
      transform: translate3d(0, 100%, 0);
    }
  
    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
  }
  
  .fadeInUp {
    -webkit-animation-name: fadeInUp;
    animation-name: fadeInUp;
  }

  @-webkit-keyframes fadeOut {
    from {
      opacity: 1;
    }
  
    to {
      opacity: 0;
    }
  }
  
  @keyframes fadeOut {
    from {
      opacity: 1;
    }
  
    to {
      opacity: 0;
    }
  }
  
  .fadeOut {
    -webkit-animation-name: fadeOut;
    animation-name: fadeOut;
  }
  
body {
    font-family: "proxima-nova";
    font-size: 1rem;
    text-align: center;
    margin: 0;
}
.header {
    background: #fff url(../img/bg-main-no_gradient-1920px.jpg) 50% 50% fixed no-repeat;
    background-size: cover;
    position: relative;
    padding: 1px 0;

    .trigger {
        margin-top: 150vh;
        margin-bottom: 50vh;
    }

    &:before {
        position: fixed;
        content: ' ';
        background-image: linear-gradient( rgba(255,255,255,1), rgba(255,255,255,0) );
        width: 100%;
        left: 0;
        top: 0;
        height: 40vh;
        display: block;
    }
    &:after {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: linear-gradient( rgba(255,255,255,0) 90vh, rgba(255,255,255,1) );
        display: block;
    }
    &__top {
        z-index: 1;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        img {
            display: block;
            margin: 0 auto;
            &.logo {
                margin-top: 2vh;
                max-height: 8vh;   
                max-width: 350px;
            }
            &.happy-holidays {
                margin-top: 2vh;
                max-height: 35vh;
                width: 90%;
                max-width: 850px;
                margin-bottom: 8vh;
            }
            &.from-our-family {
                max-width: 600px;
                &.fadeInUp {
                    
                }
            }
        }
    }
}
